import { Container, Search, Wrapper, Input, User, Avatar, AvatarName } from "./navbarStyles";
import MainButton from "../../ui/button/MainButton";
import { AccountCircleOutlined, SearchOutlined, VideoCallOutlined } from "@mui/icons-material";
import { Link, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import avatar from "../../assets/avatar.png";
import { useEffect, useState } from "react";
import Upload from "../upload/Upload";
import UserMenu from "../user-menu/UserMenu";
import { publicRequest } from "../../requestMethod";

const Navbar = () => {
    const navigate =useNavigate();
    const [user, setUser] = useState({});
    const [open, setOpen] = useState(false);
    const [openMenu, setOpenMenu] = useState(false);
    const [q, setQ] = useState("")
    const { currentUser } = useSelector(state => state.user);

    const userId = currentUser?._id

    useEffect(() => {
        const fetchUser= async () => {
            const res = await publicRequest.get(`/users/find/${userId}`);
            setUser(res.data)
        }
        fetchUser()
    }, [userId])

    return (
        <>
            <Container>
                <Wrapper>
                    <Search>
                        <Input
                            onChange={(e) => setQ(e.target.value)}
                            placeholder="Search"
                        />
                        <SearchOutlined onClick={() => navigate(`/search?q=${q}`)}/>
                    </Search>
                    {currentUser ? (
                        <User onClick={() => setOpenMenu(true)}>
                            <VideoCallOutlined
                                onClick={() => setOpen(true)}
                            />
                            <Avatar
                                src={!user?.img ? avatar : user?.img}
                            />
                            <AvatarName>{user.name}</AvatarName>
                        </User>
                    ) : (
                        <Link to="signin">
                            <MainButton>
                                <AccountCircleOutlined/>
                                Sigh in
                            </MainButton>
                        </Link>
                    )}

                </Wrapper>
            </Container>
            {open && <Upload setOpen={setOpen} setOpenMenu={setOpenMenu} />}
            {openMenu && <UserMenu setOpenMenu={setOpenMenu} />}
        </>

    );
};

export default Navbar;