import { VisibilityOffOutlined, VisibilityOutlined } from "@mui/icons-material";
import MainButton from "../../ui/button/MainButton";
import { CircularProgress } from "@mui/material";
import { ErrorContainer, ErrorText, Form, Greeting, Input, InputContainer, InputShow } from "./FormsStyle";
import { useDispatch, useSelector } from "react-redux";
import { useState } from "react";
import { useForm } from "react-hook-form";
import {
    loginUpFailure,
    loginUpStart,
    loginUpSuccess, logout
} from "../../redux/userSlice";
import { publicRequest } from "../../requestMethod";
import Cookies from "js-cookie";

const SignupForm = () => {
    const dispatch = useDispatch();
    const [greeting, setGreeting] = useState(false)
    const {isFetching, error} = useSelector((state) => state.user);
    const [showPassword, setShowPassword] = useState(false);
    const [errorAxios, setErrorAxios] = useState("");

    const handleClickShowPassword = () => setShowPassword((show) => !show);

    const {
        register,
        formState: {
            errors,
            isValid
        },
        handleSubmit,
    }
        = useForm({
        mode: "onBlur"
    });
    const handleLoginUp = async (data) => {
        dispatch(loginUpStart());
        try {
            const res = await publicRequest.post("/auth/signup", data);
            dispatch(loginUpSuccess(res.data));
            dispatch(logout());
            localStorage.clear();
            Cookies.remove('access_token');
            setGreeting(true);
            setTimeout(() => setGreeting(true), 1000);
        } catch (e) {
            dispatch(loginUpFailure(e));
            setErrorAxios(e.response.data.message);
        }
    };

    return (
        <Form>
            {greeting && (
                <InputContainer>
                    <Greeting>
                        The account has been created. Thank you for joining AirTube. Log in please :)
                    </Greeting>
                </InputContainer>
            )}
            <Input
                placeholder="name"
                {...register('name', {
                    required: "field is required",
                    minLength: {
                        value: 2,
                        message: "Username cannot be less than 2 letters"
                    }
                })}
            />
            <ErrorContainer>
                {errors?.name &&
                    <ErrorText>
                        {errors?.name?.message ||
                            "Error"
                        }
                    </ErrorText>
                }
            </ErrorContainer>
            <Input
                placeholder="email"
                type="email"
                {...register('email', {
                    required: "field is required",
                    pattern: {
                        value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                        message: "Invalid email"
                    }
                })}
            />
            <ErrorContainer>
                {errors?.email &&
                    <ErrorText>
                        {errors?.email?.message ||
                            "Error"
                        }
                    </ErrorText>
                }
            </ErrorContainer>
            <InputContainer>
                <Input
                    placeholder="password"
                    type={showPassword ? 'text' : 'password'}
                    {...register('password', {
                        required: "field is required",
                    })}
                />
                <InputShow onClick={handleClickShowPassword}>
                    {!showPassword ? <VisibilityOffOutlined /> : <VisibilityOutlined />}
                </InputShow>
            </InputContainer>

            <ErrorContainer>
                {errors?.password &&
                    <ErrorText>
                        {errors?.password?.message ||
                            "Error"
                        }
                    </ErrorText>
                }
            </ErrorContainer>
            <MainButton
                onClick={handleSubmit(handleLoginUp)}
                type="submit"
                disabled={!isValid}
            >
                {isFetching
                    ? <CircularProgress color="inherit" size="15px"/>
                    : "Sign up"
                }
            </MainButton>
            <ErrorContainer>
                {error && (
                    <ErrorText>{errorAxios}</ErrorText>
                )}

            </ErrorContainer>
        </Form>
    );
};

export default SignupForm;