import styled from "@emotion/styled";

export const Container = styled.div`
  flex: 1;
  position: sticky;
  top: 0;
  height: calc(100vh + 57px);
  background: ${(props) => props.theme['BG_COLOR']};
  * {
    color: ${(props) => props.theme['TEXT_COLOR']};
  }
`;
export const Wrapper = styled.div`
  padding: 18px 26px;
`;
export const Logo = styled.div`
  display: flex;
  align-items: center;
  font-weight: 900;
  font-size: 1.7vmin;
  margin-bottom: 25px;
  gap: 3px;
  * {
    fill: ${(props) => props.theme['ACCENT_COLOR']} !important;
  }
`;

export const Hr = styled.div`
  margin: 15px 0;
  border-bottom: 0.5px solid ${(props) => props.theme['COLOR_SOFT']};
  opacity: 0.6;
`;
export const Item = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  padding: 7.5px 0;
  transition: all 1s;
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;
export const Login = styled.div`
  
`;
export const LoginTitle = styled.div`
  margin-bottom: 10px;
`;

export const Title = styled.div`
  font-weight: 500;
  color: ${(props) => props.theme['TEXT_SOFT']} !important;
  margin: 20px 0;
  text-transform: uppercase;
`;