import Home from "../pages/home/Home";
import Video from "../pages/video-page/Video";
import { Route, Routes } from "react-router-dom";
import Signin from "../pages/signin/Signin";
import SearchPage from "../pages/search/SearchPage";
import UserPage from "../pages/user-page/UserPage";
import UserVideosPage from "../pages/user-videos-page/UserVideosPage";


const AppRouter = () => {
    return (
        <Routes>
            <Route path="/">
                <Route index element={<Home type="random" />}></Route>
                <Route path="trends" element={<Home type="trends" />}></Route>
                <Route path="subscriptions" element={<Home type="sub" />}></Route>
                <Route path="account/:id" element={<UserPage />}></Route>
                <Route path="user/:userName/:id" element={<UserVideosPage />}></Route>
                <Route path="search" element={<SearchPage />}></Route>
                <Route path="signin" element={<Signin />}></Route>
                <Route path="video">
                    <Route path=":id" element={<Video />}></Route>
                </Route>
            </Route>
        </Routes>
    );
};

export default AppRouter;