import { Container, ItemContainer, Close, List } from "./UserMenuStyle";
import { LogoutOutlined, ManageAccountsOutlined } from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../redux/userSlice";
import { Link, useNavigate } from "react-router-dom";
import Cookies from 'js-cookie'

const UserMenu = ({setOpenMenu}) => {
    const { currentUser } = useSelector(state => state.user);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const handleClick = () => {
        try {
            dispatch(logout());
            localStorage.clear();
            Cookies.remove('access_token');
            setOpenMenu(false);
            navigate("/");
        } catch (e) {
            console.log(e)
        }

    }

    return (
        <Container>
            <Close onClick={() => setOpenMenu(false)}>X</Close>
            <List>
                <ItemContainer>
                    <ManageAccountsOutlined/>
                    <Link to={ `account/${currentUser._id}` }>
                        Update account
                    </Link>
                </ItemContainer>
                <ItemContainer onClick={handleClick}>
                    <LogoutOutlined/>
                    Logout
                </ItemContainer>
            </List>
        </Container>
    );
};

export default UserMenu;