import { Close, Container, Title, Wrapper, Input, Desc, Label, Button } from "./UploadStyles";
import { useEffect, useState } from "react";
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage"
import app from "../../fb";
import { useNavigate } from "react-router-dom";
import { publicRequest } from "../../requestMethod";

const Upload = ({setOpen, setOpenMenu}) => {
    const [img, setImg] = useState(undefined);
    const [video, setVideo] = useState(undefined);
    const [imgPrc, setImgPrc] = useState(0);
    const [videoPrc, setVideoPrc] = useState(0);
    const [inputs, setInputs] = useState({});
    const [tags, setTags] = useState([]);

    const navigate = useNavigate()

    const handleTags = (e) => {
        setTags(e.target.value.split(','));
    }
    const handleChange = (e) => {
        setInputs((prev) => {
            return { ...prev, [e.target.name]: e.target.value }
        });
    }

    const uploadFile = (file, urlType) => {

            const storage = getStorage(app);
            const fileName = new Date().getTime() + file.name;

            const storageRef = ref(storage, `videos/${fileName}`);
            const uploadTask = uploadBytesResumable(storageRef, file);

        uploadTask.on('state_changed',
            (snapshot) => {
                const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                urlType === "imgUrl" ? setImgPrc(Math.round(progress)) : setVideoPrc(Math.round(progress));
                switch (snapshot.state) {
                    case 'paused':
                        console.log('Upload is paused');
                        break;
                    case 'running':
                        console.log('Upload is running');
                        break;
                    default:
                        break;
                }
            },
            (error) => {
                console.log(error)
            },
            () => {
                getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                                setInputs((prev) => {
                                    return { ...prev, [urlType]: downloadURL }
                                });
                });
            }
        );
    }

    useEffect(() => {
        video && uploadFile(video, "videoUrl")
    }, [video]);
    useEffect(() => {
       img && uploadFile(img, "imgUrl")
    }, [img]);

    const handleUpload = async (e) => {
        setOpenMenu(false);
        e.preventDefault();
        try {
            setOpenMenu(false);
            const res = await publicRequest.post("/videos", {...inputs, tags});
            setOpen(false);
            res.status === 200 && navigate(`/video/${res.data._id}`)
        } catch (e) {
            console.log(e)
        }
    }

    return (
        <Container>
            <Wrapper>
                <Close onClick={() => setOpen(false)}>X</Close>
                <Title>Upload a new video</Title>
                <Label>Video:</Label>
                {videoPrc > 0 ? (
                    "Uploading:" + videoPrc + "%"
                ) : (
                        <Input
                    onChange={ (e) => setVideo(e.target?.files[0]) }
                    type="file"
                    accept="video/*"
                />
                    )}
                <Input
                    onChange={handleChange}
                    type="text"
                    name="title"
                    placeholder="Title"
                />
                <Desc
                    onChange={handleChange}
                    placeholder="Description"
                    cols="60"
                    rows="5"
                    name="desc"
                />
                <Input
                    onChange={handleTags}
                    type="text"
                    placeholder="Separate the tags with commas: sun,music"
                />
                <Label>Image:</Label>
                {imgPrc > 0
                    ? `Uploading: ${imgPrc}%`
                     : <Input
                            onChange={(e) => setImg(e.target?.files[0])}
                            type="file"
                            accept="image/*"
                        />
                }
                <Button
                    onClick={handleUpload}
                    style={{width: "max-content"}}
                >
                    Upload
                </Button>
            </Wrapper>
        </Container>
    );
};

export default Upload;