import React, { useEffect, useState } from 'react';
import { ChannelImage, ChannelName, Container, Desc, Details, Image, Info, Title } from "./cardStyles";
import banner from "../../assets/youtube-banner.gif";
import avatar from "../../assets/avatar.png";
import { Link } from "react-router-dom";
import { format } from "timeago.js";
import { publicRequest } from "../../requestMethod";

const Card = ({type, video}) => {
    const [channel, setChannel] = useState({});

    useEffect(() => {
        const fetchChannel= async () => {
            const res = await publicRequest.get(`/users/find/${video.userId}`);
            setChannel(res.data)
        }
        fetchChannel()
    }, [video.userId])
    return (
        <Container type={type}>
            <Link to={ `/video/${video._id}` }>
            <Image type={type} src={!video?.imgUrl ? banner : video?.imgUrl} alt="banner"/>
            </Link>
            <Details type={type}>
                {channel?.name && (
                    <Link to={`/user/${channel?.name.split(" ").join("-").toLowerCase()}/${video?.userId}`}>
                        <ChannelImage
                            type={type}
                            src={!channel?.img ? avatar : channel?.img}
                            alt={video.title}
                        />
                    </Link>
                )}
                <Link to={ `/video/${video._id}` }>
                    <Desc>
                        <Title>{video.title}</Title>
                        <ChannelName>{channel?.name}</ChannelName>
                        <Info>{video.views} views • {format(video.createdAt)}</Info>
                    </Desc>
                </Link>
            </Details>
        </Container>
    );
};

export default Card;