import { Avatar, ContainerComment, DateComment, DetailsComment, NameComment, TextComment } from "./CommentsStyles";
import avatar from "../../assets/avatar.png";
import { useEffect, useState } from "react";
import { format } from "timeago.js";
import { publicRequest } from "../../requestMethod";
import { Link } from "react-router-dom";
const Comment = ({comment}) => {
    const [channel, setChannel] = useState({});
    useEffect(() => {
        const fetchComment = async () => {
            try {
                const res = await publicRequest.get(`/users/find/${comment.userId}`);
                setChannel(res.data)
            } catch (e) {
                console.log(e)
            }
        }
        fetchComment()
    }, [comment.userId])

    return (
        <ContainerComment>
            {channel.name && (
                <Link to={`/user/${channel.name.split(" ").join("-").toLowerCase()}/${comment.userId}`}>
                    <Avatar
                        src={!channel?.img ? avatar : channel?.img}
                        alt="avatar"
                    />
                </Link>
            )}
            <DetailsComment>
                <NameComment>
                    {channel.name}
                    <DateComment>
                        { format(comment.createdAt) }
                    </DateComment>
                    <TextComment>{comment.desc}</TextComment>
                </NameComment>
            </DetailsComment>
        </ContainerComment>
    );
};

export default Comment;