import {
    Container,
    Form,
    Input,
    InputContainer,
    Label,
    ErrorContainer,
    ErrorText,
    InputShow,
    InputRelative
} from "./UserPageStyle";
import MainButton from "../../ui/button/MainButton";
import { CircularProgress } from "@mui/material";
import { useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { VisibilityOffOutlined, VisibilityOutlined } from "@mui/icons-material";
import { getDownloadURL, getStorage, ref, uploadBytesResumable } from "firebase/storage";
import app from "../../fb";
import { publicRequest } from "../../requestMethod";


const UserPage = () => {
    const [inputs, setInputs] = useState("");
    const [user, setUser] = useState({});
    const {isFetching, error, currentUser} = useSelector((state) => state.user);
    const [showPassword, setShowPassword] = useState(false);
    const [errorAxios, setErrorAxios] = useState("");
    const [file, setFile] = useState(null);
    const [imgPrc, setImgPrc] = useState(0);
    const navigate = useNavigate();
    const userId = currentUser?._id;

    useEffect(() => {
        const fetchUser= async () => {
            const res = await publicRequest.get(`/users/find/${userId}`);
            setUser(res.data)
        }
        fetchUser()
    }, [userId])

    const path = useLocation().pathname.split("/")[2];

    const handleClickShowPassword = () => setShowPassword((show) => !show);

    const handleChange = (e) => {
        setInputs(prev => {
            return {...prev, [e.target.name]: e.target.value}
        });
    }

        useEffect(() => {
            const uploadImg = (file) => {
                if (file) {
                    const fileName = new Date().getTime() + file.name;
                    const storage = getStorage(app);
                    const storageRef = ref(storage, `users/${fileName}`);
                    const uploadTask = uploadBytesResumable(storageRef, file);

                    uploadTask.on('state_changed',
                        (snapshot) => {
                            const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                            setImgPrc(Math.round(progress))
                            switch (snapshot.state) {
                                case 'paused':
                                    console.log('Upload is paused');
                                    break;
                                case 'running':
                                    console.log('Upload is running');
                                    break;
                                default:
                                    break;
                            }
                        },
                        (error) => {
                            console.log(error)
                        },
                        () => {
                            getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                                setInputs((prev) => {
                                    return { ...prev, img: downloadURL }
                                });
                            });
                        }
                    );
                }
            }
            uploadImg(file)
        }, [file]);


    console.log(inputs)
    const handleUpdate = async (e) => {
        e.preventDefault();
        try {
            const res = await publicRequest.put(`/users/${path}`, inputs);
            res.status === 200 && navigate(`/`)
            navigate(0)
        } catch (e) {
            setErrorAxios(e.response.data.message);
        }
    };

    return (
        <Container>
            <Form>
                <InputContainer>
                    <Label>Update name</Label>
                    <Input
                        placeholder={user.name}
                        type="text"
                        name="name"
                        onChange={handleChange}
                    />
                </InputContainer>
                <InputContainer>
                    <Label>Update image</Label>
                    {imgPrc > 0 ? (
                        `Uploading: ${imgPrc}%`
                    ) : (
                        <Input
                            type="file"
                            placeholder={user?.img}
                            id="file"
                            name="img"
                            accept=".png,.jpeg,.jpg"
                            onChange={(e) => setFile(e.target.files[0])}
                        />
                    )}
                </InputContainer>

                <InputContainer>
                    <Label>Update email</Label>
                    <Input
                        placeholder={user.email}
                        name="email"
                        type="email"
                        onChange={handleChange}
                    />
                </InputContainer>
                <InputContainer>
                    <Label>Update password</Label>
                    <InputRelative>
                        <Input
                            placeholder="Change password"
                            name="password"
                            type={showPassword ? 'text' : 'password'}
                            onChange={handleChange}
                        />
                        <InputShow onClick={handleClickShowPassword}>
                            {!showPassword
                                ? <VisibilityOffOutlined />
                                : <VisibilityOutlined />
                            }
                        </InputShow>
                    </InputRelative>
                </InputContainer>
                <MainButton
                    onClick={handleUpdate}
                    type="submit"
                >
                    {isFetching
                        ? <CircularProgress color="inherit" size="15px"/>
                        : "Update"
                    }
                </MainButton>
                <ErrorContainer>
                    {error && (
                        <ErrorText>{errorAxios}</ErrorText>
                    )}
                </ErrorContainer>
            </Form>
        </Container>
    );
};

export default UserPage;