import { Avatar, AvatarInfo, Container, ContainerAvatar } from "./UserVideosPageStyle";
import Card from "../../components/card/Card"
import { publicRequest } from "../../requestMethod";
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

const UserVideosPage = () => {
    const [videos, setVideos] = useState([]);
    const path = useLocation().pathname.split("/")[3];

    useEffect(() => {
        const fetchVideos = async () => {
            try {
                const res = await publicRequest.get(`/videos/random`);
                setVideos(res.data)
            } catch (e) {
                console.log(e)
            }
        }
        fetchVideos()
    }, []);

    const [user, setUser] = useState({});

    useEffect(() => {
        const fetchChannel= async () => {
            const res = await publicRequest.get(`/users/find/${path}`);
            setUser(res.data)
        }
        fetchChannel()
    }, [path])

    return (
        <Container>
            <ContainerAvatar>
                <Avatar src={user.img} alt={user.name}/>
                <AvatarInfo>{user.name}</AvatarInfo>
            </ContainerAvatar>
            {videos.map(video => (
                path === video.userId && (
                    <Card video={video} key={video._id}/>
                )
            ))}
        </Container>
    );
};

export default UserVideosPage;