import styled from "@emotion/styled";

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 56px);
`;
export const Wrapper = styled.div`
  background: ${(props) => props.theme['BG_LIGHTER']};
  border: 1px solid ${(props) => props.theme['ACCENT_COLOR']};
  border-radius: 8px;
  padding: 20px 50px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
`;
export const Title = styled.h1`
    font-size: 2.4vmin;
`;
export const Subtitle = styled.h2`
    font-size: 2vmin;
  font-weight: 300;
`;
export const More = styled.div`
    display: flex;
  margin-top: 10px;
    font-size: 1.2vmin;
  color: ${(props) => props.theme['TEXT_SOFT']};
  * {
    font-size: 1.2vmin;
    color: ${(props) => props.theme['TEXT_SOFT']};
  }
`;
export const Links = styled.div`
    margin-left: 50px;
`;
export const LinkSignin = styled.span`
    margin-left: 20px;
`;
export const Button = styled.button`
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 5px 15px;
  background: rgba(255, 255, 255, 0);
  text-transform: uppercase;
  color: var(--color-accent) !important;
  border: 2px solid var(--color-accent);
  border-radius: 5px;
  cursor: pointer;
  transition: all 1s;
  * {
    color: var(--color-accent) !important;
  }
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;
