import styled from "@emotion/styled";
import MainButton from "../../ui/button/MainButton";
import { md } from "../../responsive";

export const Container = styled.div`
`;
export const NewComment = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  margin: 40px 0;
  gap: 20px;
  ${md({ flexDirection: "column" })}
`;
export const Avatar = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
`;
export const Input = styled.input`
  border: none;
  border-bottom: 0.5px solid ${(props) => props.theme['COLOR_SOFT']} !important;
  background: transparent;
  outline: none;
  padding: 5px 10px;
  width: 75%;
`;
export const ContainerComment = styled.div`
  display: flex;
  gap: 20px;
  margin: 30px 0;
`;
export const DetailsComment = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;
export const NameComment = styled.span`
    font-size: 1.3vmin;
  font-weight: 500;
`;
export const DateComment = styled.span`
   font-size: 1.2vmin;
  font-weight: 200;
  color: ${(props) => props.theme['TEXT_SOFT']} !important;
  margin-left: 5px;
`;
export const TextComment = styled.div`
    font-size: 1.4vmin;
  margin-top: 10px;
`;
export const Button = styled(MainButton)`
    position: absolute;
  top: 0;
  right: 0;
  ${md({ position: "relative" })}
`;
export const ErrorContainer = styled.div`
  padding-top: 40px;
`;
export const ErrorText = styled.span`
 color: ${(props) => props.theme['COLOR_ERROR']};
 font-size: 12px;
 font-weight: 600;
`;