import {
    Container,
    Links,
    LinkSignin,
    More,
    Subtitle,
    Title,
    Wrapper,
} from "./SigninStyles";
import MainButton from "../../ui/button/MainButton";
import { useDispatch, useSelector } from "react-redux";
import { loginFailure, loginStart, loginSuccess } from "../../redux/userSlice";
import {auth, provider} from "../../fb";
import {signInWithPopup} from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { CircularProgress } from "@mui/material";
import SigninForm from "../../components/forms/SigninForm";
import SignupForm from "../../components/forms/SignupForm";
import { publicRequest } from "../../requestMethod";

const Signin = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const {isFetching} = useSelector((state) => state.user);
    const signInWithGoogle = async () => {
        dispatch(loginStart());
        signInWithPopup(auth, provider).then((result) => {
            publicRequest.post("/auth/google", {
                name: result.user.displayName,
                email: result.user.email,
                img: result.user.photoURL,
            }).then((res) => {
                dispatch(loginSuccess(res.data))
            });
            navigate("/");
        }).catch((error) => {
            dispatch(loginFailure(error));
        }) ;
    }

    return (
        <Container>
            <Wrapper>
                <Title>Sign in</Title>
                <Subtitle>to continue tu AirTube</Subtitle>
                <SigninForm />

                <Title>or</Title>

                <MainButton
                    onClick={signInWithGoogle}
                >
                    {isFetching
                        ? <CircularProgress color="inherit" size="15px"/>
                        : "Sign in with Google"
                    }
                </MainButton>

                <Title>or</Title>

                <SignupForm />
            </Wrapper>
            <More>
                English
                <Links>
                    <LinkSignin>Help</LinkSignin>
                    <LinkSignin>Privacy</LinkSignin>
                    <LinkSignin>Terms</LinkSignin>
                </Links>
            </More>
        </Container>
    );
};

export default Signin;