import styled from "@emotion/styled";

export const Form = styled.form`
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
`;
export const InputContainer = styled.div`
  position: relative;
  width: 200px;
  border-radius: 5px;
`;
export const Input = styled.input`
  width: 200px;
  border: none;
  background: ${(props) => props.theme['COLOR_HOVER']};
  padding: 10px;
  border-radius: 5px;
`;
export const InputShow = styled.div`
  position: absolute;
  top: 0;
  right: 10px;
  height: 100%;
  display: flex;
  align-items: center;
`;
export const ErrorContainer = styled.div`

`;
export const ErrorText = styled.span`
 color: ${(props) => props.theme['COLOR_ERROR']};
 font-size: 12px;
 font-weight: 600;
`;
export const Greeting = styled.div`
 position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border: 1px solid ${(props) => props.theme['COLOR_SOFT']};
  background: ${(props) => props.theme['BG_COLOR']};
`;