import { Avatar, Container, Input, NewComment, Button, ErrorContainer, ErrorText } from "./CommentsStyles";
import avatar from "../../assets/avatar.png"
import Comment from "./Comment";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { publicRequest } from "../../requestMethod";
import { Link, useNavigate } from "react-router-dom";

const Comments = ({ videoId }) => {
    const { currentUser } = useSelector(state => state.user);
    const [comments, setComments] = useState([]);
    const [errorAxios, setErrorAxios] = useState("");
    const [commentValue, setCommentValue] = useState("");
    const navigate = useNavigate();

    const handleChange = (e) => {
        setCommentValue((prev) => {
            return { ...prev, [e.target.name]: e.target.value }
        });
    }
    const handleCommit = async (e) => {
        e.preventDefault();
        try {
            const res = await publicRequest.post("/comments", {...commentValue, videoId});
            console.log(res)
            res.status === 200 && navigate(0)
        } catch (e) {
            setErrorAxios(e.response.data.message);
        }
    }

    useEffect(() => {
        const fetchComments = async () => {
            try {
                const res = await publicRequest.get(`/comments/${videoId}`);
                setComments(res.data)
            } catch (e) {
                setErrorAxios(e.response.data.message);
            }
        }
        fetchComments()
    }, [videoId]);


    return (
        <Container>
            <NewComment>
                {currentUser?.name && (
                    <Link to={`/user/${currentUser?.name.split(" ").join("-").toLowerCase()}/${currentUser._id}`}>
                        <Avatar
                            src={!currentUser?.img ? avatar : currentUser?.img }
                            alt="avatar"
                        />
                    </Link>
                )}
                <Input
                    placeholder="Add a comment..."
                    name="desc"
                    onChange={handleChange}
                />
                {currentUser && (
                    <Button onClick={handleCommit} type="submit">Add comment</Button>
                )}
                <ErrorContainer>
                    {errorAxios && (
                        <ErrorText>{errorAxios}</ErrorText>
                    )}
                </ErrorContainer>
            </NewComment>
            {comments.map(comment =>
                <Comment comment={comment} key={comment._id}/>
            )}
        </Container>
    );
};

export default Comments;