import styled from "@emotion/styled";
import { md, sm } from "./responsive";

export const ContainerPreloader = styled.div`
  align-items: center;
  background-color: ${(props) => props.theme['BG_COLOR']};
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
`;
export const Preloader = styled.img`
  border-radius: 100%;
  height: 6em;
  width: 6em;
  object-fit: cover;
`;
export const Container = styled.div`
  display: flex;
  position: relative;
`;
export const Wrapper = styled.div`
      flex: 7;
      background: ${(props) => props.theme['BG_COLOR']};
      * {
        color: ${(props) => props.theme['TEXT_COLOR']};
      }
    `;
export const Content = styled.div`
  position: relative;
  padding: 22px 96px;
  background: ${(props) => props.theme['BG_COLOR']};
  * {
    color: ${(props) => props.theme['TEXT_COLOR']};
  }
  ${sm({ padding: "22px 40px" })}
  ${md({ padding: "22px" })}
    `;