import styled from "@emotion/styled";
import { md, sm } from "../../responsive";

export const Container = styled.div`
  width: 350px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  ${sm({ width: "50%" })}
  ${md({ width: "100%" })}
`;
export const Form = styled.form`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;
export const InputShow = styled.div`
  position: absolute;
  top: 0;
  right: 10px;
  height: 100%;
  display: flex;
  align-items: center;
`;
export const InputContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
`;
export const Label = styled.label`
  color: ${(props) => props.theme['TEXT_SOFT']};
`;
export const InputRelative = styled.div`
  position: relative;
  max-width: 100%;
`;
export const Input = styled.input`
  width: 100%;
  border: none;
  background: ${(props) => props.theme['COLOR_HOVER']};
  padding: 10px;
  border-radius: 5px;
`;

export const ErrorContainer = styled.div`

`;
export const Progress = styled.div`
  position: relative;
`;
export const ErrorText = styled.span`
 color: ${(props) => props.theme['COLOR_ERROR']};
 font-size: 12px;
 font-weight: 600;
`;