import styled from "@emotion/styled";

export const Container = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: ${(props) => props.theme['POPUP_COLOR']};
  z-index: 10;
`;
export const Wrapper = styled.div`
  position: relative;
  width: 600px;
  height: 600px;
  top: 100px;
  border-radius: 5px;
  background: ${(props) => props.theme['BG_COLOR']};
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  -webkit-box-shadow: 4px 3px 16px -6px rgba(0,0,0,0.41);
  -moz-box-shadow: 4px 3px 16px -6px rgba(0,0,0,0.41);
  box-shadow: 4px 3px 16px -6px rgba(0,0,0,0.41);
`;
export const Close = styled.div`
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;
export const Label = styled.label`
  font-size: 1.4vmin;
`;
export const Title = styled.h2`
    text-align: center;
  font-size: 2.5vmin;
  text-transform: uppercase;
`;
export const Input = styled.input`
  width: 100%;
  border: none;
  background: ${(props) => props.theme['COLOR_HOVER']};
  padding: 10px 20px;
  border-radius: 5px;
`;
export const Desc = styled.textarea`
  width: 100%;
  border: none;
  background: ${(props) => props.theme['COLOR_HOVER']};
  padding: 10px 20px;
  border-radius: 5px;
`;
export const Button = styled.button`
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 5px 15px;
  background: rgba(255, 255, 255, 0);
  text-transform: uppercase;
  color: var(--color-accent) !important;
  border: 2px solid var(--color-accent);
  border-radius: 5px;
  cursor: pointer;
  transition: all 1s;
  * {
    color: var(--color-accent) !important;
  }
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;