import { Container, Hr, Item, Login, LoginTitle, Logo, Title, Wrapper } from "./menuStyles";
import { AccountCircleOutlined, SettingsBrightnessOutlined, YouTube } from "@mui/icons-material"
import { itemMenuBottom, itemMenuBottomBottom, itemMenuTop, itemMenuTopBottom } from "../../data";
import MainButton from "../../ui/button/MainButton";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";

const Menu = ({darkMode, setDarkMode}) => {
    const tops = itemMenuTop;
    const topBottoms = itemMenuTopBottom;
    const bottoms = itemMenuBottom;
    const bottomBottoms = itemMenuBottomBottom;

    const { currentUser } = useSelector(state => state.user);

    return (
        <Container>
            <Wrapper>
                <Link to={"/"}>
                    <Logo>
                        <YouTube sx={{ width: "6vmin", height: "auto" }}/>
                        AirTube
                    </Logo>
                </Link>
                {tops.map(icon => (
                    <Link key={icon.id} to={icon?.link}>
                        <Item>
                            {icon.icon}
                            {icon.name}
                        </Item>
                    </Link>
                ))}
                <Hr/>
                {topBottoms.map(icon => (
                    <Link key={icon.id} to={icon?.link}>
                        <Item>
                            {icon.icon}
                            {icon.name}
                        </Item>
                    </Link>
                ))}
                <Hr/>
                {!currentUser && (
                    <>
                    <Login>
                    <LoginTitle>
                        Sign in to like videos, comment and subscribe
                    </LoginTitle>
                    <Link to="signin">
                        <MainButton>
                            <AccountCircleOutlined/>
                            Sign in
                        </MainButton>
                    </Link>

                </Login>
                    <Hr/>
                </>
                )}
                <Title>Best of Airtube</Title>
                {bottoms.map(icon => (
                    <Link key={icon.id} to={icon?.link}>
                        <Item>
                            {icon.icon}
                            {icon.name}
                        </Item>
                    </Link>
                ))}
                <Hr/>
                {bottomBottoms.map(icon => (
                    <Link key={icon.id} to={icon?.link}>
                        <Item>
                            {icon.icon}
                            {icon.name}
                        </Item>
                    </Link>
                ))}
                <Item onClick={() => setDarkMode(!darkMode)}>
                    <SettingsBrightnessOutlined />
                    {darkMode ? "Light" : "Dark" } Mode
                </Item>


            </Wrapper>
        </Container>
    );
};

export default Menu;