import { VisibilityOffOutlined, VisibilityOutlined } from "@mui/icons-material";
import MainButton from "../../ui/button/MainButton";
import { CircularProgress } from "@mui/material";
import { ErrorContainer, ErrorText, Form, Input, InputContainer, InputShow } from "./FormsStyle";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { loginFailure, loginStart, loginSuccess } from "../../redux/userSlice";
import { publicRequest } from "../../requestMethod";

const SigninForm = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [showPassword, setShowPassword] = useState(false);
    const {isFetching, error} = useSelector((state) => state.user);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [errorAxios, setErrorAxios] = useState("");
    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleLogin = async (e) => {
        e.preventDefault();
        dispatch(loginStart());
        try {
            const res = await publicRequest.post("/auth/signin", { email, password });
            if (res.status === 404) {
                setErrorAxios(e.response.data.message);
            } else {
                dispatch(loginSuccess(res.data));
                navigate("/");
            }
        } catch (e) {
            dispatch(loginFailure(e));
            setErrorAxios(e.response.data.message);
        }
    };

    return (
        <Form>
            <Input
                onChange={e => setEmail(e.target.value)}
                placeholder="email"
            />
            <InputContainer>
                <Input
                    onChange={e => setPassword(e.target.value)}
                    type={showPassword ? 'text' : 'password'}
                    placeholder="password"
                />
                <InputShow onClick={handleClickShowPassword}>
                    {!showPassword ? <VisibilityOffOutlined /> : <VisibilityOutlined />}
                </InputShow>
            </InputContainer>
            <MainButton
                onClick={handleLogin}
                type="submit"
                disabled={email === "" || password === ""}
            >
                {isFetching
                    ? <CircularProgress color="inherit" size="15px"/>
                    : "Sign in"
                }
            </MainButton>
            <ErrorContainer>
                {error && (
                    <ErrorText>{errorAxios}</ErrorText>
                )}

            </ErrorContainer>
        </Form>
    );
};

export default SigninForm;