import styled from "@emotion/styled";
import { md } from "../../responsive";

export const Container = styled.div`
  position: sticky;
  top: 0;
  background: ${(props) => props.theme['BG_LIGHTER']};
  height: 56px;
    * {
      color: ${(props) => props.theme['TEXT_COLOR']};
    }
`;
export const Wrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  padding: 0 20px;
`;
export const Search = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 20px;
  background: ${(props) => props.theme['COLOR_HOVER']};
  border-radius: 8px;
  * {
    fill: var(--color-grey) !important;
  }
  ${md({ margin: "0 0 0 10px" })}
`;
export const Input = styled.input`
  border: none;
  background: transparent;
  outline: none;
`;
export const User = styled.div`
  display: flex;
  align-items: center;
  gap: 10px; 
  font-weight: 500;
  cursor: pointer;
`;
export const Avatar = styled.img`
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
`;
export const AvatarName = styled.div`
  ${md({ display: "none" })}
`;