import { useEffect, useState } from 'react';
import {
    Buttons,
    Container,
    Content,
    Details,
    Info,
    Title,
    VideoWrapper,
    Button,
    Hr,
    Channel,
    ChannelInfo,
    ChannelButton,
    ChannelDetail,
    ChannelName,
    ChannelCounter,
    ChannelDesc,
    ChannelImage,
    VideoFrame, ErrorLike
} from "./videoPageStiles";
import {
    AddTaskOutlined,
    ReplyOutlined,
    ThumbDown,
    ThumbDownOutlined,
    ThumbUp,
    ThumbUpOutlined
} from "@mui/icons-material";
import avatar from "../../assets/avatar.png";
import Comments from "../../components/comments/Comments";
import { useDispatch, useSelector } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import { dislike, fetchFailure, fetchStart, fetchSuccess, like, views } from "../../redux/videoSlice";
import { format } from "timeago.js";
import { subscription } from "../../redux/userSlice";
import Recommendation from "../../components/recomendation/Recommendation";
import { publicRequest } from "../../requestMethod";

const Video = () => {
    const { currentUser } = useSelector(state => state.user);
    const { currentVideo } = useSelector(state => state.video);
    const [open, setOpen] = useState(false);
    const dispatch = useDispatch();

    const path = useLocation().pathname.split("/")[2];
    const [channel, setChannel] = useState({});

    useEffect(() => {
        dispatch(fetchStart())
        const fetchData = async () => {
            try {
                const videoRes = await publicRequest.get(`/videos/find/${path}`);
                const channelRes = await publicRequest.get(`/users/find/${videoRes.data.userId}`);
                setChannel(channelRes.data);
                dispatch(fetchSuccess(videoRes.data))
                if (videoRes) {
                    await publicRequest.put(`/videos/view/${currentVideo?._id}`)
                    dispatch(views(path))
                }
            } catch (e) {
                dispatch(fetchFailure(e))
            }
        }
        fetchData();
    }, [path, dispatch, currentVideo?._id]);

    const handleLike = async () => {
        try {
            await publicRequest.put(`/users/like/${currentVideo._id}`);
            dispatch(like(currentUser._id))
        } catch (e) {
            console.log(e)
            setOpen(true);
        }
    }
    const handleDislike = async () => {
        try {
            await publicRequest.put(`/users/dislike/${currentVideo._id}`);
            dispatch(dislike(currentUser._id))
        } catch (e) {
            console.log(e)
            setOpen(true);
        }
    }

    const handleSub = async () => {
        try {
            currentUser?.subscribedUsers.includes(channel?._id)
                ? await publicRequest.put(`/users/unsub/${channel?._id}`)
                : await publicRequest.put(`/users/sub/${channel?._id}`);

            dispatch(subscription(channel?._id));
        } catch (e) {
            console.log(e)
            setOpen(true);
        }

    }

    return (
        <Container>
            <Content>
                <VideoWrapper>
                    <VideoFrame
                        src={currentVideo?.videoUrl}
                        controls
                        autoPlay={true}
                    />
                </VideoWrapper>
                <Title>{currentVideo?.title}</Title>
                <Details>
                    <Info> {currentVideo?.views} views • {format(currentVideo?.createdAt)}</Info>
                    <Buttons>
                        <Button onClick={handleLike}>
                            {currentVideo?.likes.includes(currentUser?._id)
                                ? <ThumbUp/>
                                : <ThumbUpOutlined/> }
                            {currentVideo?.likes.length}
                        </Button>
                        <Button onClick={handleDislike}>
                            {currentVideo?.dislikes.includes(currentUser?._id)
                                ? <ThumbDown />
                                : <ThumbDownOutlined/> }
                            Dislike
                        </Button>
                        <Button>
                            <ReplyOutlined />
                            Share
                        </Button>
                        <Button>
                            <AddTaskOutlined />
                            Save
                        </Button>
                    </Buttons>
                </Details>
                {open && (
                    <ErrorLike>To respond to a video, please register or sign in</ErrorLike>
                )}
                <Hr/>
                <Channel>
                    <ChannelInfo>
                        {channel?.name && (
                            <Link to={`/user/${channel?.name.split(" ").join("-").toLowerCase()}/${currentVideo?.userId}`}>
                                <ChannelImage
                                    src={!channel?.img ? avatar : channel?.img}
                                    alt="avatar"
                                />
                            </Link>
                        )}
                        <ChannelDetail>
                            <ChannelName>{channel?.name}</ChannelName>
                            <ChannelCounter>{channel?.sub} subscribes</ChannelCounter>
                            <ChannelDesc>{currentVideo?.desc}</ChannelDesc>
                        </ChannelDetail>
                    </ChannelInfo>
                    <ChannelButton onClick={handleSub}>{
                        currentUser?.subscribedUsers.includes(channel?._id)
                            ? "Subscribed"
                            : "Subscribe"
                    }</ChannelButton>
                </Channel>
                <Hr />
                <Comments videoId={currentVideo?._id} />
            </Content>
            <Recommendation tags={currentVideo?.tags}/>
        </Container>
    );
};

export default Video;