import styled from "@emotion/styled";
import { md, sm } from "../../responsive";

export const Container = styled.div`
  display: flex;
  ${md({ flexDirection: "column" })}
  gap: 24px;
`;
export const Content = styled.div`
  position: relative;
  flex: 5;
  ${sm({ flex: "3" })}
  ${md({ flex: "unset" })}
`;
export const VideoWrapper = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  border-radius: 5px;
`;
export const Title = styled.h1`
  font-size: 1.8vmin;
  font-weight: 600;
  margin: 20px 0 10px 0;
`;
export const Details = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  *{
    color: ${(props) => props.theme['TEXT_SOFT']} !important;
  }
`;
export const Info = styled.span`
  
`;
export const Hr = styled.div`
  margin: 15px 0;
  border-bottom: 0.5px solid ${(props) => props.theme['COLOR_SOFT']};
  opacity: 0.6;
`;
export const Buttons = styled.div`
  display: flex;
  gap: 20px;
  *{
    color: ${(props) => props.theme['TEXT_COLOR']} !important;
  }
`;
export const Button = styled.div`
 display: flex;
  position: relative;
  align-items: center;
  gap: 5px;
  cursor: pointer;
`;
export const Channel = styled.div`
  display: flex;
  justify-content: space-between;
`;
export const ChannelInfo = styled.div`
  display: flex;
  gap: 20px;
  ${md({ flexDirection: "column" })}
`;
export const ChannelImage = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
`;
export const ChannelDetail = styled.div`
  display: flex;
  flex-direction: column;
`;
export const ChannelName = styled.span`
  font-size: 1.8vmin;
  font-weight: 500;
`;
export const ChannelCounter = styled.span`
  font-weight: 200;
  font-size: 1.2vmin;
  margin: 5px 0 20px 0;
  color: ${(props) => props.theme['TEXT_SOFT']} !important;
`;
export const ChannelDesc = styled.p`
  font-size: 1.5vmin;
`;
export const ErrorLike = styled.div`
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translateX(-50%);
  padding: 20px;
  border: 1px solid ${(props) => props.theme['COLOR_SOFT']};
  background: ${(props) => props.theme['BG_COLOR']};
  font-size: 1.5vmin;
  color: ${(props) => props.theme['COLOR_ERROR']} !important;
  text-transform: uppercase;
`;
export const ChannelButton = styled.button`
  background: ${(props) => props.theme['ACCENT_COLOR']} !important;
  border: none;
  border-radius: 5px;
  height: max-content;
  padding: 10px 20px;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
  transition: all 1s;
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']} !important;
    border: 2px solid ${(props) => props.theme['ACCENT_COLOR']} !important;
  }
`;
export const VideoFrame = styled.video`
  width: 50%;
  max-height: 1054px;
  border-radius: 5px;
  object-fit: cover;
  ${md({ width: "100%" })}
`;