import styled from "@emotion/styled";

export const Container = styled.div`
  position: absolute;
  padding: 20px 60px 20px 20px;
  top: 57px;
  right: 20px;
  border: 1px solid ${(props) => props.theme['COLOR_SOFT']};
  background: ${(props) => props.theme['BG_COLOR']};
  z-index: 10;
`;
export const List = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;
export const ItemContainer = styled.div`
  display: flex;
  align-items: center;
  padding: 10px 20px;
  gap: 20px;
  cursor: pointer;
  transition: all 1s;
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;
export const Close = styled.div`
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 5px;
  right: 5px;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  cursor: pointer;
  transition: all 1s;
  &:hover {
    background: ${(props) => props.theme['COLOR_HOVER']};
  }
`;